import React,{Component} from 'react';
const url = 'http://localhost:4000'
export default class Pedidos extends Component{
	constructor(props){
		super(props)
		this.state = {pedidos:null,mesas:null}
	}
	async componentDidMount(){
		let req1 = await fetch(url + '/database/pedidos')
		let req2 = await fetch(url + '/database/mesas')
		let res1 = await req1.json()
		let res2 = await req2.json()
		var pedidos = []
		for(let pedido of res1){
			var spedido = <span key={pedido.id_pedido}> {pedido.nome} </span>
			pedidos.push(spedido)
		}
		var mesas = []
		for(let mesa of res2){
			var smesa = <option key={mesa.id_mesa} value={mesa.nome}> {mesa.nome} </option>
			mesas.push(smesa)
		}
		this.setState({pedidos,mesas})
	}
	async _insertPedido(data){
		let req = await fetch(url + '/insert/pedidos',{
			method:'POST',
			headers:{
				'Content-Type':'application/json',
				'Accept':'aplication/json'
			},
			body:data
		})
		let res = await req.json()
		console.log(res)
	}
	_click(){
		var nome = document.querySelector('#nome').value
		var categoria = document.querySelector('#categoria').value
		var preco = document.querySelector('#preco').value
		var descricao = document.querySelector('#descricao').value
		var img = document.querySelector('#img').value
		
		var data = JSON.stringify({nome,categoria,preco,descricao,img})
		this._insertPedido(data)
	}
	render(){
	  return (
	   <div>
		{this.state.mesas}
	   </div>
	  )
	}
}
