import React,{Component} from 'react'
import maresia from '../maresia.jpg'
import Pedidos from './Pedidos'
const url = 'http://localhost:4000'
export default class Home extends Component{
	constructor(props){
		super(props)
		this.state = {main:<div>
			<input id="user" type="text" /> <br/>
			<input id="senha" type="password"/><br/>
			<input type="button" onClick={this._click.bind(this)} value="entrar"/>
			</div>
		}
	}
	async _verify(){
		var user = localStorage.getIytem("user")
		return user
	}
	async _signin(nome){
		localStorage.setItem("user",nome)
	}
	async _login(user,senha){
		var data = JSON.stringify({user,senha})
		let req = await fetch(url + '/garcon',{
			method:'POST',
			headers:{
				'Content-Type':'application/json',
				'Accept':'aplication/json'
			},
			body:data
		})
		let res = await req.json()
		if(res.user){
			this._signin(user)
			this.setState({main:<Card/>})
		}
	}
	_click(){
		var user = document.querySelector('#user').value
		var senha = document.querySelector('#senha').value
		
		this._login(user,senha)
	}
	render(){
	  return (
	   <div>
			{this.state.main}
		</div>
	  )
}
}

class Card extends Component{
	render(){
		return (
			 <div className="row">
				<div className="col s12 m7">
				  <div className="card">
					<div className="card-image">
					  <img src={maresia}/>
					  <span className="card-title">Card Title</span>
					</div>
					<div className="card-content">
					  <p>I am a very simple card. I am good at containing small bits of information.
					  I am convenient because I require little markup to use effectively.</p>
					</div>
					<div className="card-action">
					  <a href="#">This is a link</a>
					</div>
				  </div>
				</div>
			 </div>
		)
	}
}
